    text-shadow: 0 0 4px #f9731680;
  }

  50% {
    text-shadow: 0 0 12px #f97316cc;
  }

  to {
    text-shadow: 0 0 4px #f9731680;
  }
}

.ai-glow {
  animation: 2s ease-in-out infinite pulseGlow;
}

.animate-fadeIn {
  animation: .6s ease-in-out forwards fadeIn;
}

@keyframes pulse-slow {
  0%, to {
    opacity: .9;
    transform: scale(1);
  }

  50% {
    opacity: 1;
    transform: scale(1.03);
  }
}

.animate-pulse-slow {
  animation: 3s ease-in-out infinite pulse-slow;
}

